<template>
  <div class="inspection-info">
    <NavigatorBar title="" bgColor="#fff" navType="light"/>
    <div class="inspection-panel" :style="{backgroundColor: panelColor}">
      <p class="info"></p>
      <p class="num">{{remainingDay}}<em></em></p>
      <p class="desc">2018430{{otherDesc}}</p>
    </div>
    <div class="reminding-switch" @click="tapSwitch">
      <div class="info">
        <p class="title"></p>
        <p class="desc">90/30/10/3</p>
      </div>
      <div :class="['choose-btn', switchActive? 'active': '']"></div>
    </div>
    <div class="collapse">
      <div :class="['collapse-item', showCollapse == 1 ? 'active' : '']">
        <div class="title" @click="tapCollapse(1)">
          <span></span>
          <img src="../assets/pages/InspectionInfo/arrow-down.png" class="arrow-down">
        </div>
        <div class="detail">
          <p></p>
          <p>1. 51561</p>
          <p>2. 1011061</p>
          <p>3. 42141</p>
          <p>4. 6</p>
          <p>5. 6</p>
          <p>6. 1</p>
          <p>7.611561</p>
          <p>3</p>
        </div>
      </div>
      <div :class="['collapse-item', showCollapse == 2 ? 'active' : '']">
        <div class="title" @click="tapCollapse(2)">
          <span></span>
          <img src="../assets/pages/InspectionInfo/arrow-down.png" class="arrow-down">
        </div>
        <div class="detail">
          <p></p>
        </div>
      </div>
      <div :class="['collapse-item', showCollapse == 3 ? 'active' : '']">
        <div class="title" @click="tapCollapse(3)">
          <span></span>
          <img src="../assets/pages/InspectionInfo/arrow-down.png" class="arrow-down">
        </div>
        <div class="detail">
          <p>1. </p>
          <p>2. </p>
        </div>
      </div>
      <div :class="['collapse-item', showCollapse == 4 ? 'active' : '']">
        <div class="title" @click="tapCollapse(4)">
          <span></span>
          <img src="../assets/pages/InspectionInfo/arrow-down.png" class="arrow-down">
        </div>
        <div class="detail">
          <p></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import NavigatorBar from '@/components/NavigatorBar/NavigatorBar'
  export default {
    name: "inspection-info",
    data() {
      return {
        panelColor: '#45A4F7',
        remainingDay: 373,
        otherDesc: '',
        switchActive: true,
        showCollapse: 0,
      }
    },
    components: {
      NavigatorBar
    },
    mounted() {
      /*  */
      window.scrollTo(0, 0)
      if(this.$route.query.type == 2){
        this.panelColor = '#FBCB61'
        this.remainingDay = 12
        this.otherDesc = ''
      }
    },
    methods: {
      tapSwitch() {
        this.switchActive = !this.switchActive
      },
      tapCollapse(num){
        if(num == this.showCollapse){
          this.showCollapse = 0
        } else {
          this.showCollapse = num
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .inspection-info {
    margin-top: 44px;
    .inspection-panel {
      padding: 30px;
      color: #fff;
      text-align: center;
      .info {
        font-size: 13px;
        line-height: 13px;
        margin-bottom: 20px;
      }
      .num {
        font-size: 48px;
        line-height: 48px;
        margin-bottom: 30px;
        em {
          font-style: normal;
          font-size: 15px;
          line-height: 15px;
          margin-left: 5px;
        }
      }
      .desc {
        font-size: 11px;
      }
    }
    .reminding-switch {
      background: #fff;
      padding: 10px 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
      .info {
        .title {
          font-size: 13px;
          line-height: 13px;
          color: #333;
          margin-bottom: 10px;
        }
        .desc {
          font-size: 11px;
          line-height: 11px;
          color: #666;
        }
      }
      .choose-btn {
        box-shadow: #ccc 0 0 0 1px;
        width: 40px;
        height: 20px;
        border-radius: 20px;
        position: relative;
        background-color: #bdbdbd;
        overflow: hidden;
        float: right;
        &:before {
          content: '';
          position: absolute;
          left: 0;
          width: 20px;
          height: 20px;
          border-radius: 20px;
          background-color: #fff;
          z-index: 20;
          transition: all 0.5s;
        }
        &.active {
          background-color: #45A4F7;
          &:before {
            left: 20px;
          }
        }
      }
    }
    .collapse {
      padding-left: 15px;
      background: #fff;
      .collapse-item {
        .title {
          font-size: 13px;
          line-height: 48px;
          border-bottom: 1px solid #dbdbdb;
          position: relative;
          .arrow-down {
            width: 12px;
            height: 7px;
            position: absolute;
            right: 15px;
            top: 50%;
            margin-top: -3px;
            transition: 300ms;
          }
        }
        .detail {
          padding: 0 15px 0 0;
          height: 0;
          overflow: hidden;
          transition: 300ms;
          border-bottom: 1px solid #dbdbdb;
          margin-top: -1px;
          p {
            font-size: 12px;
            line-height: 18px;
            margin-bottom: 5px;
            color: #666666
          }
        }
        &.active {
          .title {
            .arrow-down {
              transform: rotate(180deg);
            }
          }
          .detail {
            padding-top: 10px;
            padding-bottom: 10px;
            height: auto;
          }
        }
        &:last-child {
          .title {
            border-bottom: 0;
          }
          &.active {
            .title {
              border-bottom: 1px solid #dbdbdb;
            }
          }
          .detail {
            border-bottom: 0;
          }
        }
      }
    }
  }

</style>



